/* App.css */

.App {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #333;
}

.horiz {
  text-align: right;
}

.horiz h1 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.current-time {
  margin-top: 15px;
  font-size: 18px;
  color: #666;
}

.date-navigation button {
  background-color: #979797;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 2px 6px;
  margin-right: 3px;
  margin-top: 6px;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.date-navigation button:hover {
  background-color: #727272;
}

.date-navigation button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5);
}
.progress-container {
  margin-bottom: 10px;
}

.progress-label {
  margin-bottom: 5px;
}

.progress-bar-container {
  width: 100%;
  background-color: #ddd;
  height: 20px;
  margin-top: 5px;
  border-radius: 4px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #4caf50;
  text-align: right;
  padding-right: 5px;
  line-height: 20px;
  box-sizing: border-box;
  overflow: hidden;
  transition: width 0.3s ease-in-out; 
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

table th, table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

table th {
  background-color: #f2f2f2;
}

table tr:nth-child(even) {
  background-color: #f9f9f9;
}

table tr:hover {
  background-color: #e9e9e9;
}

.completed {
  text-decoration: line-through;
}

.centerize {
  text-align: center;
}

table td input[type="checkbox"] {
  margin: 0 auto; 
  display: block; 
}

.gray {
  color: #bcbcbc;
}

.margin {
  margin: 50px 0 30px 0;
}

.fixed {
  position: sticky;
  top: 0;
  padding: 20px 0 15px 0;
  background: linear-gradient(to bottom,
  rgba(240, 240, 240, 1) 10%,
  rgba(240, 240, 240, 1) 80%,
  rgba(240, 240, 240, 0.2) 90%
);
}

.alignright {
  align-items: right;
}

.margin a{
  color: rgb(81, 81, 81);
  cursor: pointer;
  
}

.margin a:hover{
  color: green;
}

.margin p{
  user-select: none;
}